html{margin:0;padding:0}header{display:block;position:relative}body{font-family:"Open Sans",sans-serif;font-size:18px;color:#000;font-weight:400;font-style:normal;margin:0;padding:0;min-height:100%}.logo{position:absolute;top:50%;left:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}.nav{width:100%;justify-content:center;background-repeat:no-repeat;background-size:contain;background-position:center center;-webkit-background-size:cover;-moz-background-size:cover;-o-background-size:cover;margin:0;display:block;height:100px;background-image:url(../img/headerBg.jpg);color:#fff;background-color:grey}ul.centerUL{margin:0;line-height:5;padding-left:0}.centerUL li{display:inline;text-align:center}li{padding:30px}a{text-decoration:none}#nav a{color:#fff}#nav a:hover{color:red}a:hover{color:#00f}.nav_text{width:100%;text-align:center}.scrolled{position:fixed;top:0}footer{bottom:0}.cv{height:100%;width:100%}.centertext{text-align:center}.contruction{height:50%;width:50%}.resizeimg{width:90%;min-width:200px;height:90%;min-height:200px}.grid{text-align:center;display:grid;grid-template-columns:49%;grid-template-rows:49%}gobelet{padding-top:10%;grid-column:1;grid-row:1}tictactoe{padding-top:10%;grid-column:2;grid-row:1}article{padding-top:10%;grid-column:1;grid-row:2}aside{padding-top:10%;grid-column:2;grid-row:2}